<template>
  <div class="box">
    <LayoutBox one="我报名的活动" two="活动详情">
      <h4>活动介绍</h4>
      <div class="">
        <label>活动名称：</label>
        <span>{{ goodsList.activeName }}</span><br>
        <label>活动类型：</label>
        <span>{{ goodsList.activeType === 0 ? '团购' : '秒杀' }}</span><br>
        <label>报名时间：</label>
        <span>{{ goodsList.applyStartTime }} 至 {{ goodsList.applyEndTime }}</span><br>
        <label>活动时间：</label>
        <span>{{ goodsList.startTime }} 至 {{ goodsList.endTime }}</span><br>
        <label>活动规则：</label>
        <span>{{ goodsList.activeDescribe }}</span><br>
      </div>
    </LayoutBox>
    <el-card class="box-card">
      <h4>关联商品</h4>
      <el-table :data="goodsList.rpl">
        <!-- <el-table-column prop="code" label="商品编号" align="center"></el-table-column> -->
        <el-table-column prop="name" label="商品名称" align="center"></el-table-column>
        <el-table-column prop="shopName" label="供应商" align="center"></el-table-column>
        <el-table-column prop="postFee" label="运费" align="center"></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'ActivityContent',

  components: {
    LayoutBox
  },

  data () {
    return {
      goodsList: [], // 关联商品
    }
  },

  methods: {
    // api----------------------------------------------------------------------------------
    // 获取活动商品详情
    apiFindByActivityIdGetProduct () {
      this.$http.post(`/woho/activity/manage/findByActivityIdGetProduct/${this.$route.query.id}`)
        .then((res) => {
          this.goodsList = res.data
        })
    }
  },

  created () {
    this.apiFindByActivityIdGetProduct()
  }
}
</script>

<style lang="scss" scoped>
  .box-card{
    width: 100%;
    margin-top: 15px;
  }
</style>
